import styled from 'styled-components'
import { Link } from '@mui/material'

import telegram from "../../svg/telegram.svg"
import twitter from '../../svg/twitter.svg'
import medium from '../../svg/medium.svg'
import reddit from '../../svg/reddit.svg'

const Wrapper = styled.div`
  width:100%;
  position: fixed;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow:auto;
  background:#5F47EE;
  position:absolute;
  z-index:11;
`
const Inner = styled.div`
  max-width:1086px;
  width:90%;
  margin:50px auto 0px auto;
  display:flex;
  justify-content:space-between;
  align-items:center;
  flex-wrap:wrap;
`
const Header = styled.a`
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 64px;
  letter-spacing: -1px;
  text-transform: uppercase;
  text-decoration:none !important;
  color: #FCFCFC;
  margin-top:24px;
  cursor:pointer;
  &:hover{
    color:#A0B9FB;
  }
`
const Subheader = styled.div`
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #F0EFEF;
  margin-bottom:8px;
  @media(max-width:857px){
    margin-top:30px;
  }
`
const Subcontent = styled.div`
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
  color: #A0B9FB;
  margin-top:24px;
  cursor:pointer;
`
const Icon = styled.img`
  width:18px;
  height:18px;
  margin-left:4px;
`
const Button = styled.div`
  border: 1px solid #FCFCFC;
  box-sizing: border-box;
  border-radius: 28px;
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 0;
  text-align:center;
  padding:20px 0px 20px;
  width:304px;
  margin:18px 0px;
  cursor:pointer;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  text-transform: uppercase;
  color: #FCFCFC;
  @media(max-width:768px){
    width:100%;
  }
`
const StyledLink = styled(Link)`
  text-decoration: none !important;
  color: white !important;
  &:hover{
    text-decoration: none;
  }
`
const Social = styled.img`
  width:32px;
  height:32px;
  margin:0px 12px;
  cursor:pointer;
`
const Leftside = styled.div`
  display:block;
`
const Rightside = styled.div`
  display:block;
  @media(max-width:768px){
    width:100%;
    height:auto;
  }
`
const Subwrapper2 = styled.div`
  display:flex;
  justify-content:center;
  width:304px;
  @media(max-width:768px){
    margin:0 auto;
    margin-bottom:30px;
  }
`
const Image = styled.img`
  display:none;
  @media(max-width:705px){
    display:block;
    margin-bottom:40px;
  }
`
interface Props {
  isburger: boolean,
  SetIsBurger: React.Dispatch<React.SetStateAction<boolean>>;
}
const Burger = (props: Props) => {
  const handle = () => {
    props.SetIsBurger(!props.isburger);
  }

  return (
    <Wrapper>
      <Inner>
        <Leftside>
          <Image src="assets/svg/contract.png" />
          {Link1.map((link, index) => (
            <Header key={index} href={`#${link}`} onClick={handle}>{link}<br /></Header>
          ))}
        </Leftside>
        <Rightside>
          <Subheader>Trading Charts</Subheader>
          {Link2.map((link, index) => (
            <StyledLink key={index} href={link.url} target="_blank">
              <Subcontent>{link.title}</Subcontent>
            </StyledLink>
          ))}
          <Button>
            <StyledLink href='https://exchange.robustswap.com/#/swap?outputCurrency=0x891e4554227385c5c740f9b483e935e3cbc29f01' target='_blank'>
              BUY $RBT
              <Icon src="assets/svg/gointo2.png" />
            </StyledLink>
          </Button>
          <Button>
            <StyledLink href='https://robustswap.com' target='_blank'>
              ROBUST SWAP
            </StyledLink>
            <Icon src="assets/svg/gointo2.png" />
          </Button>
          <Subwrapper2>
            <StyledLink href="https://t.me/robustprotocol" target="_blank">
              <Social src={telegram} />
            </StyledLink>
            <StyledLink href="https://twitter.com/robustprotocol" target="_blank">
              <Social src={twitter} />
            </StyledLink>
            <StyledLink href="https://robustprotocol.medium.com/" target="_blank">
              <Social src={medium} />
            </StyledLink>
            <StyledLink href="https://reddit.com/r/robustprotocol/" target="_blank">
              <Social src={reddit} />
            </StyledLink>
          </Subwrapper2>
        </Rightside>
      </Inner>
    </Wrapper>
  )
}

const Link1 = ["The ecosystem", "Roadmap", "Features", "Token Distribution", "Contract Address"]
const Link2 = [
  {
    "title": "Poocoin",
    "url": "https://poocoin.app/tokens/0x891e4554227385c5c740f9b483e935e3cbc29f01",
  },
  {
    "title": "Dex Guru",
    "url": "https://dex.guru/token/0x891e4554227385c5c740f9b483e935e3cbc29f01-bsc",
  },
  {
    "title": "Dex Tools",
    "url": "https://www.dextools.io/app/bnb/pair-explorer/0xfb3a3e8e9a1fbfdfb237442168a96ec84a331b1f",
  },
  {
    "title": "Bogged",
    "url": "https://charts.bogged.finance/?token=0x891E4554227385c5c740F9B483E935E3CbC29F01",
  }
]
export default Burger